/* Ensure no horizontal scrolling */
body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.homepage {
  font-family: 'Arial', sans-serif;
  color: #222;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  min-height: 100vh;
  background: linear-gradient(to bottom, #007bff, #0056b3);
}

/* Header Styles */
.header {
  width: 100%;
  background-color: #0056b3;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 40px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
  box-sizing: border-box;
}

.header .logo {
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: white;
  text-decoration: none;
}

.header .menu-toggle {
  display: none;
  font-size: 1.8rem;
  cursor: pointer;
  color: white;
}

.header .nav-links {
  display: flex;
  gap: 20px;
  align-items: center;
}

.header .nav-links a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
  padding: 8px 12px;
  border-radius: 4px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.header .nav-links a:hover {
  background-color: #ffcc00;
  color: #0056b3;
}

/* Mobile View */
@media (max-width: 768px) {
  .header {
    flex-wrap: wrap;
  }

  .header .menu-toggle {
    display: block;
  }

  .header .nav-links {
    display: none;
    flex-direction: column;
    width: 100%;
    background-color: #0056b3;
    padding: 10px 0;
    border-radius: 0 0 10px 10px;
  }

  .header .nav-links.active {
    display: flex;
  }

  .header .nav-links a {
    text-align: center;
    padding: 10px 0;
    width: 100%;
  }
}

/* Hero Section */
.hero {
  text-align: center;
  padding: 60px 20px;
  color: white;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.hero p {
  font-size: 1.3rem;
  margin-bottom: 25px;
}

.hero button {
  background-color: #ff4444;
  color: white;
  border: none;
  padding: 14px 28px;
  font-size: 1.1rem;
  cursor: pointer;
  border-radius: 8px;
  transition: background 0.3s ease, transform 0.2s ease;
}

.hero button:hover {
  background-color: #dd3333;
  transform: scale(1.05);
}

/* How It Works Section */
.how-it-works {
  text-align: center;
  background-color: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 1200px;
  margin: 30px 0;
}

.how-it-works h2 {
  font-size: 2rem;
  margin-bottom: 25px;
  color: #333;
}

.steps {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 25px;
  justify-items: center;
  padding: 0 10px;
  box-sizing: border-box;
}

.step {
  text-align: center;
  background: white;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.step:hover {
  transform: translateY(-8px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.step .icon {
  font-size: 2.5rem;
  color: #007bff;
  margin-bottom: 15px;
}

.step h3 {
  font-size: 1.3rem;
  margin-bottom: 12px;
  color: #333;
}

.step p {
  font-size: 1rem;
  color: #555;
}

/* Why Free Section */
.why-free {
  background-color: #f5f5f5;
  padding: 40px 40px;
  text-align: center;
  border-radius: 12px;
  width: 100%;
  margin: 30px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.why-free h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
  color: #333;
}

.why-free p {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.6;
  padding: 5px 40px;
}

/* Footer Section */
.footer {
  background-color: #111;
  color: #eee;
  text-align: center;
  font-size: 1rem;
  width: 100%;
  box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.1);
  border-top: 2px solid #444;
}

.footer p {
  margin: 0;
  font-size: 0.9rem;
  line-height: 1.6;
}

.footer a {
  color: #ffcc00;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease, text-decoration 0.3s ease;
}

.footer a:hover {
  color: #fff;
  text-decoration: underline;
}

.footer .social-icons {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  gap: 15px;
}

.footer .social-icons a {
  color: #ffcc00;
  font-size: 1.5rem;
  transition: color 0.3s ease, transform 0.3s ease;
}

.footer .social-icons a:hover {
  color: #fff;
  transform: scale(1.2);
}

@media (max-width: 768px) {
  .header {
    flex-wrap: wrap;
  }

  .header .nav-links {
    width: 100%;
  }

  .hero {
    padding: 20px 10px;
  }

  .steps {
    grid-template-columns: repeat(1, 1fr); /* 1 column for mobile */
    gap: 15px; /* Adjust the gap to be smaller */
  }

  .step {
    padding: 20px; /* Adjust padding for smaller screens */
  }

  .footer .social-icons {
    gap: 10px;
  }
}